export default{
	data(){
		return{
			userInfo:{},
			firstSection:[
				{
					index:0,
					title:'会员号'
				},
				{
					index:1,
					title:'推荐人'
				},
				{
					index:2,
					title:'注册时间'
				}
			],
			
			secondSection:[
				{
					index:0,
					title:'微信绑定',
				},
				{
					index:1,
					title:'我的收获地址',
				},
				{
					index:2,
					title:'安全隐私'
				}
				
			],
		}
	},
	onLoad(){
		this.getUserInfoWithKey('userInfo').then((info)=>{
			console.log(info);
			this.userInfo=info;
		})
	},
	methods:{
		async changehead(){
			var res =await this.chooseImgAndUpload({
				count:1
			}).then((res)=>{
				console.log(res);
				var headurl = res[0];
			});
			
		},
		clickDup(){
			uni.setClipboardData({
			    data: 'kissssss'
			});
			uni.showToast({
				icon:'none',
				title:'会员号已复制到剪贴板'
			})
		},
		jump(fun){
		    if(fun.index==1){
				uni.navigateTo({
					url:'address'
				})
			}else if(fun.index==2){
				uni.navigateTo({
					url:'./safePrivacy'
				})
			}	
		},
		gotoBind(){
			
		},
		unBind(){
			uni.showModal({
				title: '提示',
				content: '确定解除绑定?',
				success: function(res) {
					if (res.confirm) {
						console.log('点击确定');
						
								
					}
				}
			})
		},
		clickOut(){
			uni.showModal({
				title: '提示',
				content: '确定退出登录?',
				success: function(res) {
					if (res.confirm) {
						console.log('点击确定');
						
								
					}
				}
			})
		},
		getImgUrl(icon) {
			return this.mainServer + icon;
		},
		dateFormat:function(time) {
			return this.getDateSecond(time);
		},
	}
}
